import { newE2EPage } from '@stencil/core/testing';

describe('presto-youtube-subscribe-button', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-youtube-subscribe-button></presto-youtube-subscribe-button>');

    const element = await page.find('presto-youtube-subscribe-button');
    expect(element).toHaveClass('hydrated');
  });
});
